</template>
<script>
export default {
  name: "companyInfo",
  data(){
      return{
          rules: {},
      ruleForm: {
        recruit_type: "",
        position_name:"",
        work_city:'',
        work_adr:'',
        exp_req:'',
        edu_req:''
      },
      }
  }
};
</script>


